import { CurrencyPipe, DatePipe } from '@angular/common';
import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NavController } from '@ionic/angular';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import { NOT_BALANCE_AVAILABLE_ERRORS } from '@nequi/features/commons/core';
import { DynatraceService, TransactionBaseService } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import {
  CreditInteractor,
  CreditType,
  OperationCreditType,
  PayCreditParam,
  PaymentEntity,
  PaymentTransaction,
  PaymentType,
  RescheduleType
} from '@nequi/features/credits/core';
import { Pocket } from '@nequi/features/money-management/core';
import { CardModalService, FullscreenModalService, ToastService } from '@nequi/ui';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import {
  AdjustInstallmentsEntity
} from '../organisms/adjust-installments/adjust-installments.organism';
import {
  AdjustIntallmentsConfig
} from '../products/propeller/pages/adjust-installments/adjust-installments.config';
import { CreditInfoModel } from './credit-info.model';

@Injectable()
export class AdjustInstallmentsService {
  public lastFeeAmount: number;
  public paymentAmount: number;
  public data: AdjustInstallmentsEntity;
  private i18n: Record<string, any>;

  constructor(
    private creditInteractor: CreditInteractor,
    private activatedRoute: ActivatedRoute,
    private navController: NavController,
    private datePipe: DatePipe,
    private currencyPipe: CurrencyPipe,
    private creditInfoModel: CreditInfoModel,
    private transactionBaseService: TransactionBaseService,
    private toastService: ToastService,
    private cardModalService: CardModalService,
    private fullscreenModalService: FullscreenModalService,
    private translateProvider: TranslateProvider,
    private dynatraceService: DynatraceService
  ) {
    this.data = {
      nextFeeDate: new Date(),
      extraMoney: 0,
      amountFeesAdjusted: 0,
      lastFeeDate: new Date(),
      numberFeesAdjusted: 0,
    };
  }

  public async loadData() {
    this.paymentAmount = this.creditInfoModel.creditFeatureInfo.paymentAmount;

    const response = await this.creditInteractor.getLoanSimulation({
      productId: this.creditInfoModel.creditFeatureInfo.productId,
      amount: this.paymentAmount,
    });

    this.data.extraMoney = response.amount;
    this.data.nextFeeDate = response.nextPaymentDate;

    response.data.forEach((item) => {
      if (AdjustIntallmentsConfig.optionList.adjustFeeAmount === item.rephraseType) {
        this.data.amountFeesAdjusted = item.newInstallmentAmount;
      }

      if (AdjustIntallmentsConfig.optionList.adjustFeeNumber === item.rephraseType) {
        this.data.lastFeeDate = item.newDueDate;
        this.data.numberFeesAdjusted = item.newNumberOfInstallments;
        this.lastFeeAmount = item.lastInstallmentAmount;
      }
    });
  }

  public async makePayment(
    i18n: Record<string, any>,
    rescheduleType: RescheduleType,
    pocket: Pocket,
    productId: CreditType
  ): Promise<void> {
    this.i18n = i18n;

    const data: PayCreditParam = {
      operatorType: OperationCreditType.PAYMENT,
      productId,
      debenture: this.creditInfoModel.creditFeatureInfo.productId,
      paymentType: PaymentType.LOAN,
      rescheduleType: rescheduleType,
    };

    const paymentTransaction = new PaymentTransaction()
      .withData(data)
      .withAmount(this.paymentAmount)
      .withPocket(pocket);

    this.transactionBaseService.executeTransaction(
      paymentTransaction,
      (response: PaymentEntity) => this.onTransactionSuccess(response, rescheduleType),
      (error: CoreException) => this.onTransactionFailed(error)
    );
  }

  protected async onTransactionSuccess(response: PaymentEntity, rescheduleType: RescheduleType) {
    await this.setAnalyticsOfPayment(rescheduleType);

    if (rescheduleType === RescheduleType.INSTALLMENTS_AMOUNT) {
      this.i18n.cardModal.reduceFeesValue.content = this.translateProvider.get(
        AdjustIntallmentsConfig.i18n.modals.reduceFeesValue,
        {
          amountFeesAdjusted: this.currencyPipe.transform(this.data.amountFeesAdjusted),
          lifeInsurance: this.currencyPipe.transform(0),
          normalInterest: this.currencyPipe.transform(response.paymentDetails.normalInterest),
          nextFeeDate: this.datePipe.transform(this.data.nextFeeDate),
        }
      );

      this.showCardModal(this.i18n.cardModal.reduceFeesValue);
    }

    if (rescheduleType === RescheduleType.INSTALLMENTS_NUMBER) {
      this.i18n.cardModal.reduceFeesNumber.content = this.translateProvider.get(
        AdjustIntallmentsConfig.i18n.modals.reduceFeesNumber,
        {
          feesNumber: String(this.data.numberFeesAdjusted),
          lastFeeAmount: this.currencyPipe.transform(this.lastFeeAmount),
          lifeInsurance: this.currencyPipe.transform(0),
          normalInterest: this.currencyPipe.transform(response.paymentDetails.normalInterest),
        }
      );

      this.showCardModal(this.i18n.cardModal.reduceFeesNumber);
    }
  }

  protected onTransactionFailed(error: CoreException) {
    this.dynatraceService.setCustomError(error);

    if (error instanceof ExceedCeilingException) {
      throw error;
    }

    if (NOT_BALANCE_AVAILABLE_ERRORS.includes(error.code)) {
      throw error;
    }

    this.toastService.showError(error.message);
  }

  private async showFullScreenModal(i18n: Record<string, string>) {
    this.fullscreenModalService.show({
      title: i18n.title,
      imgPath: i18n.image,
      content: i18n.content,
      cssClass: AdjustIntallmentsConfig.fullscreenModal.cssClass,
      fullPageDismiss: true,
      buttons: [
        {
          text: i18n.readyButton,
          handler: this.goToDashboard.bind(this),
          cssClass: AdjustIntallmentsConfig.fullscreenModal.cssClassButton,
        },
      ],
    });
  }

  private async showCardModal(i18n: Record<string, string>) {
    this.cardModalService.show({
      title: i18n.title,
      imgPath: i18n.image,
      content: i18n.content,
      buttons: [
        {
          text: i18n.readyButton,
          handler: this.goToDashboard.bind(this),
        },
      ],
    });
  }

  private goToDashboard() {
    this.navController.navigateForward(AdjustIntallmentsConfig.routes.dashboard, {
      relativeTo: this.activatedRoute,
    });
  }

  private async setAnalyticsOfPayment(rescheduleType: RescheduleType) {
    await this.dynatraceService.setAnalyticsOfEnd();
    await this.dynatraceService.setAnalyticsOfEvent({
      eventName: AdjustIntallmentsConfig.analytics.intentionToPaySuccess.name,
      eventProps: stringsArrayToObjectWithValues(
        AdjustIntallmentsConfig.analytics.intentionToPaySuccess.params.dynatrace,
        [
          CreditType.PROPELLER,
          this.translateProvider.get(AdjustIntallmentsConfig.i18n.paymentType[rescheduleType]),
          this.paymentAmount,
        ]
      ),
      leave: true,
    });
  }
}
